<template>
  <v-container fluid grid-list-md>
 
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>

        <v-card>
          <v-toolbar color="teal darken-4" dark>

            <v-toolbar-title>กลุ่ม {{groupName}}</v-toolbar-title>
          </v-toolbar>

          <v-expansion-panel>
            <v-expansion-panel-content>
              <template v-slot:header>
                <div class="teal--text"><h3>ข้อมูลกลุ่ม</h3></div>
              </template>
              <v-card >
                <v-list>
                  <v-list-tile @click="">
                    <v-list-tile-action>
                      <v-icon>G</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>{{groupId}}</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider inset></v-divider>

                  <v-list-tile @click="">
                    <v-list-tile-action>
                      <v-icon>group</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>จำนวนสมาชิก {{groupMember}} คน</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>

                  <v-divider inset></v-divider>

                  <v-list-tile @click="">
                    <v-list-tile-action>
                      <v-icon>face</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>ผู้ประสานงาน : {{coordinator}}</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider inset></v-divider>

                  <v-list-tile @click="">
                    <v-list-tile-action>
                      <v-icon>call</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>{{coordinatorTel}}</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </v-list>
              </v-card>
            </v-expansion-panel-content>

            <!--ตาราง transactions by user  -->
            <v-expansion-panel-content>
              <template v-slot:header >
                <div class="teal--text" @click.once="showTable"><h3>ประวัติการส่งยอดคำภาวนา</h3></div>
              </template>
              <v-card>
                <v-list-tile @click="">
                  <v-list-tile-action>
                    <v-btn flat icon color="pink">
                      <v-icon>grade</v-icon>
                    </v-btn>
                  </v-list-tile-action>
                  <v-list-tile-content>
                    <v-list-tile-title class="pink--text">ยอดรวมทั้งหมด: {{ total }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-data-table
                  :headers="headers"
                  :items="yod"
                  class="elevation-1"                  
                >
                  <template v-slot:items="props">
                    <td>{{ props.item.date }}</td>
                    <td class="text-xs-right">{{ props.item.yodSamma }}</td>
                  </template>
                </v-data-table>
              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-card>
      </v-flex>
    </v-layout>

  
  </v-container>
</template>

<script>
  import firebaseApp from "@/components/firebaseInit";
  export default {
    name: "view-yod-group",
    data() {
      return {
        valid: false,
        id: '',
        groupId: '',
        groupName: '',
        groupMember: '',
        coordinator: '',
        coordinatorTel: '',
        yodSamma: '',
        headers: [
          {
            text: 'วันที่ส่งยอดคำภาวนา',
            align: 'left',
            value: 'date'
          },
          { text: 'ยอดภาวนา', value: 'yodSamma' }
        ],
        yod: [],
        groups: []
      };
    },
    beforeRouteEnter(to, from, next) {
      firebaseApp.collection("yodByUser")
        .where("groupId", "==", to.params.groupId)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.id = doc.id;
              vm.groupId = doc.data().groupId;
              vm.groupName = doc.data().groupName;
              vm.groupMember = doc.data().groupMember;
              vm.coordinator = doc.data().coordinator;
              vm.coordinatorTel = doc.data().coordinatorTel;
              vm.yodSamma = doc.data().yodSamma;
            });
          });
        });
    },
    watch: {
      $route: "fetchData"
    },
    methods: {
      fetchData() {
        firebaseApp.collection("yodByUser")
          .where("groupId", "==", this.$route.params.groupId)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.id = doc.id;
              this.groupId = doc.data().groupId;
              this.groupName = doc.data().groupName;
              this.groupMember = doc.data().groupMember;
              this.coordinator = doc.data().coordinator;
              this.coordinatorTel = doc.data().coordinatorTel;
              this.yodSamma = doc.data().yodSamma;
            });
          });
      },
      async knowIdUser () {
        await firebaseApp.collection("yodByUser")
          .where("groupId", "==", this.groupId)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.id = doc.id;
              this.groupId = doc.data().groupId;
            });
          });
        console.log(this.id);
      },
      createdTable() {
        firebaseApp.collection("yodByUser")
        .doc(this.id).collection('transactions')
          // .orderBy('date', "asc")
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              // console.log(doc.data());
              const data = {
                'date': doc.data().date,
                'yodSamma': doc.data().yodSamma
              };
              this.yod.push(data);
            });
          });
      },
      async showTable () {
        await this.knowIdUser()
        await this.createdTable()
        //await this.totalAmount()
      },
      deleteGroup() {
        if (confirm("Are you sure?")) {
          firebaseApp.collection("yodByUser")
            .where("groupId", "==", this.$route.params.groupId)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete();
                this.$router.push("/group");
              });
            });
        }
      }
    },
    computed : {
      total: function() {
        let sum = 0;
        return this.yod.reduce((sum, item) => sum + item.yodSamma, 0);
      }
    }
  };
</script>


